// Define an interface for StatCard props to provide type safety
interface StatCardProps {
  title: string;
  value: string | number;
}

// Define an interface for items in the recentActivity array
interface ActivityItem {
  user: string;
  action: string;
  date: string;
}

// Optional: Create a reusable StatCard component (e.g., in components/StatCard.jsx)
function StatCard({ title, value }: StatCardProps) {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-xl p-6 shadow-lg transform hover:scale-105 transition duration-300 ease-in-out">
      <h2 className="text-lg font-medium mb-2 text-gray-700 dark:text-gray-200">{title}</h2>
      <p className="text-4xl font-extrabold text-indigo-600 dark:text-indigo-400">{value}</p>
    </div>
  );
}



